@use "global"as *;

.archive-tour_plan {

}

.archive-tour_plan-layout {
	margin-top: rem(40);

}

.archive-tour_plan__inner {

}



.archive-tour_plan__cards {

}

.archive-tour_plan-card__img {
  width: 100%;
}

.archive-tour_plan-card__img img {
  width: 100%;
  height: auto;
  aspect-ratio: 280 /188;
  object-fit: cover;

  @include mq(md) {
    aspect-ratio: 333 /223;
  }
}

.archive-plan-card__body {
  width: 100%;
  padding-top: rem(16);
  padding-bottom: rem(24);
  padding-left: rem(16);
  padding-right: rem(16);
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  @include mq(md) {
    padding-top: rem(24);
    padding-bottom: rem(44);
    padding-left: rem(24);
    padding-right: rem(24);
  }
}

.archive-plan-card__top {
  padding-bottom: rem(8);

  @include mq(md) {
    padding-bottom: rem(4);
  }
}


.archive-plan-card__category {
  display: inline-block;
  padding-left: rem(10);
  padding-right: rem(12);
  padding-top: rem(4);
  height: rem(30);
  font-size: rem(12);
  font-family: $base-font-family;
  font-weight: $regular;
  line-height: 1.83;
  letter-spacing: 0;
  color: $greenaccent;
  border: 1px solid $greenaccent;

  @include mq(md) {
    padding-left: rem(16);
    padding-right: rem(16);
    height: rem(34);
  }
}

.archive-plan-card__title {
  margin-top: rem(8);
  font-size: rem(16);
  font-family: $base-font-family;
  font-weight: $medium;
  line-height: 1.5;
  letter-spacing: 0;

  @include mq(md) {
    font-size: rem(24);
  }
}

.archive-plan-card__text {
  margin-top: auto;
  text-align: center;
  border-top: 1px solid $greenaccent;
  padding-top: rem(6);

  @include mq(md) {
    padding-top: rem(24);
  }
}


.archive-plan-card__price-info {
  font-size: rem(14);
  font-family: $base-font-family;
  font-weight: $regular;
  line-height: 1.71;
  letter-spacing: 0;
  text-align: center;

  @include mq(md) {
    font-size: rem(16);
    line-height: 1.5;
    padding-right: rem(14);
  }
}


.archive-plan-card__price-text {
  margin-top: rem(4);

  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-left: rem(20);
  padding-right: rem(18);

  @include mq(md) {
    font-size: rem(40);
    // max-width: rem(280);
    margin-inline: auto;
    width: 100%;
    padding-left: 0;
    padding-right: 0;

  }
}

.archive-plan-card__price-old {
  display: inline-block;
  margin-top: rem(16);
  font-size: rem(20);
  font-family: $base-font-family;
  font-weight: $bold;
  line-height: 1;
  letter-spacing: 0;
  color: $font-color;
  text-decoration: line-through;
  text-decoration-color: #c94800;

  @include mq(md) {
    margin-top: rem(16);
    font-size: rem(24);
    padding-right: rem(26);
  }
}

.archive-plan-card__price-new {
  margin-top: rem(4);
  font-size: rem(32);
  font-family: $base-font-family;
  font-weight: $bold;
  line-height: 1;
  letter-spacing: 0;
  color: $greenaccent;

  @include mq(md) {
    font-size: rem(40);
  }
}

.archive-plan-card__subbody {
  display: none;

  @include mq(md) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
    margin-top: rem(24);
    text-align: center;
    color: $greenaccent;
  }
}

.archive-plan-card__subtext {
  font-size: rem(16);
  font-family: $base-font-family;
  font-weight: $regular;
  line-height: 1.5;
  letter-spacing: 0;
  color: $font-color;
  text-align: left;
  flex-grow: 1;
}

.archive-plan-card__meta {
  margin-top: auto;
}

.archive-plan-card__date {
  margin-top: rem(32);
  font-size: rem(16);
  font-family: $base-font-family;
  font-weight: $bold;
  line-height: 1;
  letter-spacing: 0;
  padding-right: rem(16);
}

.archive-plan-card__microcopy {
  margin-top: rem(20);
  font-size: rem(14);
  font-family: $base-font-family;
  font-weight: $regular;
  line-height: 1;
  letter-spacing: 0;
}

.archive-plan-card__button {
  margin-top: rem(10);
  text-align: center;

  @include mq(md) {
    margin-top: rem(10);
  }
}
