.button-send-container {
  position: relative;
  /* ボタンと画像を重ねるために relative を使用 */
  display: inline-block;
  /* ボタンをインラインブロックにすることで幅と高さを指定 */
}

.button-send-container::before {
  content: "";
  position: absolute;
  width: 202px;
  height: 54px;
  top: 4px;
  left: 4px;
  border: 1px solid #000074;
  z-index: -1;
}

.button-send-container::after {
  content: "";
  position: absolute;
  right: 56px;
  top: 50%;
  transform: translateY(calc(-50% - 3px)) skew(45deg);
  width: 40px;
  height: 6px;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  z-index: 100;
}

.button-send {
  position: relative;
  /* 画像の位置をボタンに対して設定するために relative を使用 */
  z-index: 1;
  /* 画像より前面に表示 */
