  vertical-align: bottom;
  display: inline-block;
  text-align: left;
  padding: 16px 0 14px 56px;
  background-color: #000074;
  color: white;
  font-size: 14px;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  line-height: 1.57;
