/* チェックボックス全体のラッパー */
.wpcf7-form-control-wrap {
  margin-top: 16px;
}

@media (min-width: 768px) {
  .wpcf7-form-control-wrap {
    margin-top: 0;
  }
}
/* チェックボックスのラベルとチェックボックス自体 */
.wpcf7-form-control.wpcf7-checkbox {
  position: relative;
  display: block;
  /* チェックボックスを縦並びに */
  padding-left: 10px;
  cursor: pointer;
  user-select: none;
  line-height: 1.7;
}

/* チェックボックスの input 要素を非表示に */
.wpcf7-form-control.wpcf7-checkbox input[type=checkbox] {
  display: none;
}

/* チェックボックスマークのスタイル */
.wpcf7-form-control.wpcf7-checkbox input[type=checkbox] + .wpcf7-list-item-label::before {
  content: "";
  position: absolute;
  top: 50%;
  /* チェックボックスの中央に配置 */
  transform: translateY(-50%);
  left: 0;
  height: 14px;
  /* チェックボックスのサイズ */
  width: 14px;
  border: solid 2px #4CAF50;
  /* 線の色（元の $greenaccent に相当） */
  box-sizing: border-box;
}

/* エラー時のチェックボックスマーク */
.wpcf7-form-control.wpcf7-checkbox input[type=checkbox] + .wpcf7-list-item-label--error::before {
  border-radius: 50%;
}

/* 選択時に表示するチェックマーク */
.wpcf7-form-control.wpcf7-checkbox input[type=checkbox]:checked + .wpcf7-list-item-label::after {
  content: "";
  position: absolute;
  width: 4px;
  height: 8px;
  top: 50%;
  left: 3px;
  transform: translateY(-50%) rotate(45deg);
  border-right: 2px solid #c94800;
  /* チェックマークの色 */
  border-bottom: 2px solid #c94800;
  /* チェックマークの色 */
  opacity: 1;
  /* チェックされたときに表示 */
}

/* エラー時のチェックマーク */
.wpcf7-form-control.wpcf7-checkbox input[type=checkbox]:checked + .wpcf7-list-item-label--error::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #408F95;
  /* エラーチェック時の背景色 */
  opacity: 1;
  /* 表示する */
}

/* セレクトボックス全体のラッパー */
.form__select {
  margin-top: 1rem;
}
@media screen and (min-width: 768px) {
  .form__select {
    margin-top: 0;
  }
}

/* セレクトボックスのアイテムを包むdiv */
.form__select-items {
  display: inline-block;
  position: relative;
  width: 100%;
  height: 2.5rem;
  /* 高さはpxからremに変更 */
  /* Contact Form 7の構造に対応 */
}
@media screen and (min-width: 768px) {
  .form__select-items {
    height: 1.875rem;
    /* メディアクエリで高さを調整 */
  }
}
.form__select-items .wpcf7-form-control-wrap {
  width: 100%;
  /* 親要素と同じ幅に */
}

/* セレクトボックスの右側に表示される三角形アイコン */
.form__select-items::after {
  content: "";
  position: absolute;
  right: 8px;
  /* 右端からの位置 */
  top: 50%;
  /* 中央に配置 */
  transform: translateY(-50%);
  /* Y軸中央揃え */
  width: 12px;
  height: calc(6px * tan(60deg));
  /* 三角形の高さ */
  clip-path: polygon(0 0, 100% 0, 50% 100%);
  /* 三角形のクリップ */
  background: #000074;
  /* 三角形の色 */
}

/* 三角形アイコンを非表示にするクラス */
.form__select-items.no-triangle::after {
  display: none;
}

/* セレクトボックスのスタイル */
.form__select-item,
.wpcf7-select {
  appearance: none;
  /* デフォルトのセレクトボックスのスタイルを無効化 */
  -webkit-appearance: none;
  /* Chrome/Safari対応 */
  -moz-appearance: none;
  /* Firefox対応 */
  background-color: white;
  /* 背景色 */
  border: 1px solid #000074;
  /* 枠線の色 */
  width: 100%;
  /* 幅は親要素にフィット */
  height: inherit;
  /* 親要素の高さに合わせる */
  padding-left: 0.5rem;
  /* テキストの左側の余白 */
  font-size: 0.75rem;
  /* 文字サイズ */
  font-family: "Noto Sans JP", sans-serif;
  /* フォント */
  font-weight: 400;
  /* フォントの重さ */
  line-height: 3;
  letter-spacing: 0;
  /* 文字間の余白 */
}

