@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////
$base-font-family: "Noto Sans JP", sans-serif;
$second-font-family: "Noto Serif JP", serif;



// ※検証ツールでしっかり反映されているか確認する



// フォントウェイト
$regular: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black:#000;
$white: #fff;
$brown:#796A64;
$font-color:#000074;
$greenaccent: #000074;
$greenpale: #D6E6F5;
$accent:#ffff00;


body {
  font-family: $base-font-family;
	background-color: #fff;
	color:$font-color;
	line-height: 1.7;


}
