@use "global" as *;

.section-heading {
  text-align: center;
  position: relative;
}

.section-heading__title {
	display: block;
  text-align: center;
  color: $greenaccent;
  font-size: rem(32);
  font-family: $base-font-family;
  font-weight: $regular;
  letter-spacing: 0;

  @include mq(md) {
    font-size: rem(40);

  }
}

.section-heading__subtitle {
	display: block;
  font-size: rem(14);
  font-weight: $regular;
  font-family: $base-font-family;
  letter-spacing: 0;
  color: $greenaccent;
  @include mq(md) {

  }
}

.section-heading__title--top-blog {
  @include mq(md) {
    color: $greenaccent;
  }
}



.section-heading__title.section-heading__title--contact {
  font-size: rem(80);
  line-height: 1;
  letter-spacing: 0;
  @include mq(md) {
    letter-spacing: 0;
  }
}
.section-heading__subtitle.section-heading__subtitle--contact {
  margin-top:rem(-4);
  @include mq(md) {
    margin-top: rem(-6);
    line-height: 1;
  }
}
.section-heading__title.section-heading__title--local-media,
.section-heading__subtitle.section-heading__subtitle--local-media {
	color:$greenaccent;
	@include mq(md) {
		color:$white;
	}
}

.section-heading__text {
  margin-top: rem(6);
  font-size: rem(14);
  font-family: $base-font-family;
  font-weight: $regular;
  line-height: 1.285;
  letter-spacing: 0;
  color: $greenaccent;
  @include mq(md) {
    margin-top: rem(14);
  }
}
