@use "global"as *;

.archive-plan-cards {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  place-items: center;
  gap: rem(40);

  @include mq(md) {
    display: grid;
    justify-content: space-between;
    grid-template-columns: 1fr 1fr;
    // grid-template-rows: 1fr 1fr;
    column-gap: calc(calc(40/1080)*100%);
    row-gap: rem(78);
  }
}

.archive-plan-cards__item {
  width: 100%;
  max-width: rem(280);
  display: flex;
  /* 子要素をフレックスボックスで配置 */
  align-items: stretch;
  /* 高さを揃える */
  @include mq(md) {
    max-width: rem(520);
  }
}
